.project_N {
  background: #e1e0ff;
  min-width: calc(40% + 20px * 2);
  height: calc(300px + 20px * 2);
  margin: 20px;
  /* padding: 40px; */
  border-radius: 30px;
  border: 4px solid #121111;
  display: flex;
  flex-direction: column;
  /* flex-grow: 1; */
  /* background-image: url(./rolgusCom.png); */

  overflow: auto;
}
.project_N > * {
  transition: all 0.5s;
  border-end-end-radius: 30px;
  backdrop-filter: blur(8px);
}

.project_N-image {
  position: relative;
  top: 0;
  left: 0;
}

.project_N-name {
  flex-grow: 1;
}

.project_N-link {
  flex-grow: 1;
}
.project_N > * {
  display: flex;
  flex-wrap: wrap;
}
.project_N > * > * {
  margin: auto;
}

.project_N-hover {
  height: 100%;
  width: 100%;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  /* flex-grow: 1; */
  background: #807f9160;
}
.project_N-link button {
  height: 15px;
  padding: 15px;
  cursor: pointer;
  border-style: solid;
  border-width: 2px;
  border-color: #121111;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 847px) {
  .project_N {
    min-width: calc(70% + 20px * 2);
  }
}
